才的世界顶级科研团队创造全球一流的科研条件。鉴于疫情紧急，恒大支持专家组工作的首期2亿元资金已经到位。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({backgroundImage: ("url(" + (require('../../assets/images/news/company_hf_info2.png')) + ")")})}),_c('h6',[_vm._v("恒大哈佛高管团队合影")])]),_vm._m(0)])]),_c('infoPage',{attrs:{"first":_vm.first,"last":_vm.last,"prevUrl":_vm.prevUrl,"nextUrl":_vm.nextUrl}})],1)}
